interface TopicProgress {
  topic_id: string
  student_id: string
  video_xp: number
  test_xp: number
  revision_xp: number
  total_xp: number
  status: "MASTERED" | "GOOD" | "NEEDS_WORK" | "Not Started"
  progress_percentage: number
  test_score: number
  revision_1_completed: boolean
  revision_2_completed: boolean
  revision_3_completed: boolean
  revision_4_completed: boolean
}

interface Revision {
  topic_id: string
  student_id: string
  revision_number: number
  due_date: string
  is_completed: boolean
  is_overdue: boolean
}

interface TestScore {
  id: string
  student_id: string
  topic_id: string
  test_material_id: string
  score: number
  max_score: number
  created_at: string
}

interface GroupedMaterials {
  video: StudyMaterial[]
  notes: StudyMaterial[]
  pdf: StudyMaterial[]
  slides: StudyMaterial[]
  test: StudyMaterial[]
  "test-solution": StudyMaterial[]
  reference: StudyMaterial[]
  extra: StudyMaterial[]
}

interface TestAttempt {
  id: string
  student_id: string
  topic_id: string
  test_material_id: string
  attempt_number: number
  score: number
  max_score: number
  created_at: string
}

interface StudentProfile {
  id: string
  total_points: number
  current_level: number
  xp_to_next_level: number
}

interface XPEvent {
  id: string
  student_id: string
  topic_id: string
  material_id: string
  event_type: string
  xp: number
  created_at: string
}

// Cooldown durations in milliseconds
const COOLDOWN_DURATION = 180000 // 3 minutes
const COOLDOWN_CHECK_DURATION = 360000 // 6 minutes

// Level Bar Component - with better error handling
function LevelBar({ studentId }: { studentId: string }) {
  const [profile, setProfile] = useState<StudentProfile | null>(null)
  const [loading, setLoading] = useState(true)
  const [showLevelUp, setShowLevelUp] = useState(false)
  const supabase = createClient()
  const prevLevelRef = useRef<number>(0)

  const fetchProfile = useCallback(async () => {
    try {
      setLoading(true)
      
      const { data, error } = await supabase
        .from("student_profiles")
        .select("id, total_points, current_level, xp_to_next_level")
        .eq("id", studentId)
        .single()

      if (error) {
        // Enhanced error logging
        const errorInfo = {
          code: error.code || "UNKNOWN",
          message: error.message || "Unknown error",
          status: error.status || "Unknown",
          hint: (error as any).hint || "No hint",
        }
        console.warn("Profile fetch warning - using default profile:", errorInfo)

        // Set default profile regardless of error
        setProfile({
          id: studentId,
          total_points: 0,
          current_level: 1,
          xp_to_next_level: 1000,
        })
        setLoading(false)
        return
      }

      if (!data) {
        console.warn("No profile data returned, using default profile")
        setProfile({
          id: studentId,
          total_points: 0,
          current_level: 1,
          xp_to_next_level: 1000,
        })
        setLoading(false)
        return
      }

      // Check for level up
      if (prevLevelRef.current > 0 && data.current_level > prevLevelRef.current) {
        setShowLevelUp(true)
        setTimeout(() => setShowLevelUp(false), 2000)
      }

      prevLevelRef.current = data.current_level
      setProfile(data)
    } catch (err) {
      console.error("Exception in profile fetch:", err)
      setProfile({
        id: studentId,
        total_points: 0,
        current_level: 1,
        xp_to_next_level: 1000,
      })
    } finally {
      setLoading(false)
    }
  }, [studentId, supabase])

  useEffect(() => {
    fetchProfile()
    const interval = setInterval(fetchProfile, 5000)
    return () => clearInterval(interval)
  }, [fetchProfile])

  if (loading || !profile) {
    return <div className="h-20 bg-muted rounded-lg animate-pulse" />
  }

  const xpProgress = ((1000 - profile.xp_to_next_level) / 1000) * 100

  return (
    <Card className={`transition-all ${showLevelUp ? "ring-2 ring-yellow-400" : ""}`}>
      <CardContent className="p-4">
        <div className="space-y-2">
          <div className="flex items-center justify-between">
            <Badge variant="default" className={`text-lg py-2 px-3 ${showLevelUp ? "animate-bounce" : ""}`}>
              Level {profile.current_level}
            </Badge>
            <span className="text-sm font-medium text-muted-foreground">
              {profile.total_points.toLocaleString()} points
            </span>
          </div>
          <Progress value={Math.min(xpProgress, 100)} className="h-2" />
          <p className="text-xs text-muted-foreground text-right">
            {Math.max(profile.xp_to_next_level, 0)} XP to next level
          </p>
        </div>
      </CardContent>
    </Card>
  )
}

// XP Popup Animation Component
function XPPopup({ xp, show }: { xp: number; show: boolean }) {
  if (!show) return null

  return (
    <div className="fixed top-20 right-4 z-50 animate-bounce">
      <div className="flex items-center gap-2 bg-yellow-400 text-yellow-900 px-4 py-2 rounded-lg font-bold shadow-lg">
        <Zap className="h-5 w-5" />
        +{xp} XP
      </div>
    </div>
  )
}

// Study Materials Component with improved XP System
function StudyMaterials({ materials, studentId, topicId, onXPAward }: { materials: StudyMaterial[]; studentId: string; topicId: string; onXPAward: (eventType: any, materialId: string, score?: number) => Promise<void> }) {
  const [testAttempts, setTestAttempts] = useState<Record<string, TestAttempt[]>>({})
  const [testInputs, setTestInputs] = useState<Record<string, number | undefined>>({})
  const [submittingTests, setSubmittingTests] = useState<Record<string, boolean>>({})
  const [retestingTests, setRetestingTests] = useState<Record<string, boolean>>({})
  const [loadingScores, setLoadingScores] = useState(true)
  const [expandedHistory, setExpandedHistory] = useState<Record<string, boolean>>({})
  const [showXPPopup, setShowXPPopup] = useState<{ show: boolean; xp: number }>({ show: false, xp: 0 })
  const [showConfetti, setShowConfetti] = useState(false)
  const supabase = createClient()
  const { toast } = useToast()

  const groupMaterials = (items: StudyMaterial[]): GroupedMaterials => {
    const grouped: GroupedMaterials = {
      video: [],
      notes: [],
      pdf: [],
      slides: [],
      test: [],
      "test-solution": [],
      reference: [],
      extra: [],
    }

    items.forEach((item) => {
      if (item.content_type in grouped) {
        grouped[item.content_type as keyof GroupedMaterials].push(item)
      }
    })

    return grouped
  }

  // Fetch all test attempts
  useEffect(() => {
    const fetchTestAttempts = async () => {
      setLoadingScores(true)
      try {
        const testMaterials = materials.filter((m) => m.content_type === "test")
        
        const attempts: Record<string, TestAttempt[]> = {}
        
        for (const material of testMaterials) {
          const { data, error } = await supabase
            .from("tests")
            .select("*")
            .eq("student_id", studentId)
            .eq("test_material_id", material.id)
            .order("attempt_number", { ascending: false })

          if (!error && data) {
            attempts[material.id] = data
          } else {
            attempts[material.id] = []
          }
        }
        
        setTestAttempts(attempts)
      } catch (err) {
        console.error("Error fetching test attempts:", err)
      } finally {
        setLoadingScores(false)
      }
    }

    if (materials.length > 0) {
      fetchTestAttempts()
    }
  }, [materials, studentId, topicId, supabase])

  // Anti-cheat: Check cooldown from localStorage
  const checkLocalCooldown = (key: string): boolean => {
    try {
      const cooldowns = JSON.parse(localStorage.getItem("xp_cooldowns") || "{}")
      const lastTime = cooldowns[key]
      
      if (!lastTime) return true
      
      const elapsed = Date.now() - lastTime
      return elapsed > COOLDOWN_DURATION
    } catch {
      return true // Allow on error
    }
  }

  // Anti-cheat: Save cooldown to localStorage
  const saveCooldown = (key: string) => {
    try {
      const cooldowns = JSON.parse(localStorage.getItem("xp_cooldowns") || "{}")
      cooldowns[key] = Date.now()
      localStorage.setItem("xp_cooldowns", JSON.stringify(cooldowns))
    } catch {
      console.warn("Failed to save cooldown to localStorage")
    }
  }

  // Anti-cheat: Check backend for recent XP events - with better error handling
  const checkRecentXPEvent = async (eventType: string, materialId: string): Promise<boolean> => {
    try {
      const { data, error } = await supabase
        .from("xp_events")
        .select("id")
        .eq("student_id", studentId)
        .eq("event_type", eventType)
        .eq("material_id", materialId)
        .gte("created_at", new Date(Date.now() - COOLDOWN_CHECK_DURATION).toISOString())

      // If table doesn't exist or other error, allow the action (fail open)
      if (error) {
        console.warn("XP events check failed (table may not exist):", {
          code: error.code,
          message: error.message,
        })
        return true // Allow action if check fails
      }

      return data.length === 0 // True if no recent events (allowed)
    } catch (err) {
      console.warn("Exception checking XP events:", err)
      return true // Allow on exception
    }
  }

  // XP Action Wrapper with improved error handling
  const handleXPAction = useCallback(
    async (
      eventType: string,
      materialId: string,
      rpcFunction: () => Promise<any>,
      xpAmount: number = 0
    ) => {
      const cooldownKey = `${eventType}_${materialId}`

      // Frontend cooldown check
      if (!checkLocalCooldown(cooldownKey)) {
        toast({
          title: "Cooldown Active",
          description: "XP recently received. Try again in a few minutes.",
          variant: "destructive",
        })
        return
      }

      // Backend cooldown check (non-blocking)
      const isAllowed = await checkRecentXPEvent(eventType, materialId)
      if (!isAllowed) {
        toast({
          title: "XP Already Awarded",
          description: "You've already earned XP for this action recently.",
          variant: "destructive",
        })
        return
      }

      try {
        const { error } = await rpcFunction()

        if (error) {
          const errorInfo = {
            code: error.code || "UNKNOWN",
            message: error.message || "Unknown error",
            details: (error as any).details || "No details",
          }
          console.warn("RPC execution warning:", errorInfo)
          
          // If RPC doesn't exist, still show success for frontend feedback
          if (error.code === "42883") {
            console.warn("RPC function not found, continuing with frontend XP display")
            // Continue to show XP feedback to user
          } else {
            throw new Error(error.message || "Failed to award XP")
          }
        }

        // Save cooldown
        saveCooldown(cooldownKey)

        // Show XP popup
        setShowXPPopup({ show: true, xp: xpAmount })
        setTimeout(() => setShowXPPopup({ show: false, xp: 0 }), 1500)

        // Refresh level UI
        onXPAward?.()

        toast({
          title: "XP Earned!",
          description: `+${xpAmount} XP for your effort.`,
        })
      } catch (err) {
        const errorMsg = err instanceof Error ? err.message : "Failed to award XP"
        console.error("Error in XP action:", errorMsg)
        toast({
          title: "Error",
          description: errorMsg,
          variant: "destructive",
        })
      }
    },
    [studentId, toast, onXPAward, supabase]
  )

  // Handle video watch
  const handleVideoWatch = async (materialId: string, isRewatch: boolean = false) => {
    await handleXPAction(
      isRewatch ? "video_rewatch" : "video_watch",
      materialId,
      () =>
        supabase.rpc("give_video_xp", {
          p_student: studentId,
          p_topic: topicId,
          p_material: materialId,
          p_is_rewatch: isRewatch,
        }),
      isRewatch ? 25 : 50
    )
  }

  // Handle note read
  const handleReadNote = async (materialId: string, isRevise: boolean = false) => {
    await handleXPAction(
      isRevise ? "note_revise" : "note_read",
      materialId,
      () =>
        supabase.rpc("give_note_xp", {
          p_student: studentId,
          p_topic: topicId,
          p_material: materialId,
          p_is_revise: isRevise,
        }),
      isRevise ? 15 : 30
    )
  }

  // Handle test score submit
  const handleTestScoreSubmitWithXP = async (materialId: string, score: number, isRetest: boolean = false) => {
    const stateKey = isRetest ? `retest-${materialId}` : materialId
    setSubmittingTests((prev) => ({
      ...prev,
      [stateKey]: true,
    }))

    try {
      const currentAttempts = testAttempts[materialId] || []
      const nextAttemptNumber = currentAttempts.length > 0 
        ? Math.max(...currentAttempts.map(a => a.attempt_number)) + 1
        : 1

      const { data, error } = await supabase.from("tests").insert({
        student_id: studentId,
        topic_id: topicId,
        test_material_id: materialId,
        attempt_number: nextAttemptNumber,
        score,
        max_score: 20,
      }).select()

      if (error) throw new Error(`Failed to submit score: ${error.message}`)

      // Award XP with anti-cheat
      await handleXPAction(
        "test_score",
        materialId,
        () =>
          supabase.rpc("give_test_xp", {
            p_student: studentId,
            p_topic: topicId,
            p_material: materialId,
            p_score: score,
          }),
        Math.floor(score * 2)
      )

      if (data && data.length > 0) {
        setTestAttempts((prev) => ({
          ...prev,
          [materialId]: [data[0], ...currentAttempts],
        }))
      }

      setTestInputs((prev) => ({
        ...prev,
        [materialId]: undefined,
      }))

      setRetestingTests((prev) => ({
        ...prev,
        [materialId]: false,
      }))
    } catch (err) {
      console.error("Error submitting test score:", err)
      toast({
        title: "Error",
        description: "Failed to submit test score.",
        variant: "destructive",
      })
    } finally {
      setSubmittingTests((prev) => ({
        ...prev,
        [stateKey]: false,
      }))
    }
  }

  const handleTestScoreChange = (materialId: string, value: string) => {
    const numValue = value === "" ? undefined : Math.min(Math.max(parseInt(value) || 0, 0), 20)
    setTestInputs((prev) => ({
      ...prev,
      [materialId]: numValue,
    }))
  }

  const grouped = groupMaterials(materials)
  const hasAnyMaterials = Object.values(grouped).some((arr) => arr.length > 0)

  if (!hasAnyMaterials) {
    return (
      <Card>
        <CardContent className="pt-6">
          <p className="text-center text-sm text-muted-foreground">No study materials available for this topic yet.</p>
        </CardContent>
      </Card>
    )
  }

  const getAttemptStatus = (score: number) => {
    return score >= 12 ? { label: "PASSED", color: "bg-green-100 text-green-800" } : { label: "FAILED", color: "bg-red-100 text-red-800" }
  }

  const renderSection = (
    title: string,
    icon: string,
    items: StudyMaterial[],
    buttonLabel: string,
    isTestSection: boolean = false,
    isVideoSection: boolean = false,
    isNoteSection: boolean = false
  ) => {
    if (items.length === 0) return null

    return (
      <div key={title}>
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">
              {icon} {title}
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {items.map((item) => {
                const attempts = testAttempts[item.id] || []
                const latestAttempt = attempts.length > 0 ? attempts[0] : null
                const isShowingRetest = retestingTests[item.id]
                const currentInput = testInputs[item.id]
                const isSubmitting = submittingTests[isShowingRetest ? `retest-${item.id}` : item.id]

                return (
                  <div key={item.id} className="rounded-lg border p-4 hover:bg-muted/50">
                    <XPPopup show={showXPPopup.show} xp={showXPPopup.xp} />

                    <div className="flex items-start justify-between gap-4 mb-4">
                      <div className="flex-1">
                        <p className="font-medium text-sm">{item.title}</p>
                        <Badge variant="secondary" className="mt-2">
                          {item.content_type}
                        </Badge>
                      </div>
                      <div className="flex gap-2">
                        <Button
                          onClick={() => window.open(item.resource_url, "_blank")}
                          size="sm"
                          variant="outline"
                        >
                          {buttonLabel}
                        </Button>
                        {(isVideoSection || isNoteSection) && (
                          <Button
                            onClick={() =>
                              isVideoSection
                                ? handleVideoWatch(item.id, false)
                                : handleReadNote(item.id, false)
                            }
                            size="sm"
                            className="gap-1"
                          >
                            <Zap className="h-3 w-3" />
                            XP
                          </Button>
                        )}
                      </div>
                    </div>

                    {isTestSection && (
                      <div className="space-y-4 border-t pt-4">
                        {/* Latest Attempt Summary */}
                        {latestAttempt && !isShowingRetest && (
                          <div className="rounded-lg bg-muted p-4">
                            <div className="flex items-start justify-between">
                              <div>
                                <p className="text-sm font-semibold">
                                  Latest Score: {latestAttempt.score} / {latestAttempt.max_score}
                                </p>
                                <p className="text-xs text-muted-foreground mt-1">
                                  Attempt #{latestAttempt.attempt_number}
                                </p>
                                <p className="text-xs text-muted-foreground">
                                  Taken on: {new Date(latestAttempt.created_at).toLocaleDateString()}
                                </p>
                              </div>
                              <Badge className={getAttemptStatus(latestAttempt.score).color}>
                                {getAttemptStatus(latestAttempt.score).label}
                              </Badge>
                            </div>
                          </div>
                        )}

                        {/* New Attempt Form */}
                        {!latestAttempt || isShowingRetest ? (
                          <div className="flex items-end gap-2">
                            <div className="flex-1">
                              <label className="block text-xs font-medium text-muted-foreground mb-1">
                                Score (out of 20)
                              </label>
                              <Input
                                type="number"
                                min="0"
                                max="20"
                                placeholder="Enter your score"
                                value={currentInput ?? ""}
                                onChange={(e) => handleTestScoreChange(item.id, e.target.value)}
                                disabled={isSubmitting}
                              />
                            </div>
                            <Button
                              onClick={() => handleTestScoreSubmitWithXP(item.id, currentInput || 0, isShowingRetest)}
                              disabled={currentInput === undefined || isSubmitting}
                              size="sm"
                            >
                              {isSubmitting ? (
                                <>
                                  <Loader2 className="h-3 w-3 mr-1 animate-spin" />
                                  Submitting
                                </>
                              ) : isShowingRetest ? (
                                "Resubmit"
                              ) : (
                                "Submit"
                              )}
                            </Button>
                            {isShowingRetest && (
                              <Button
                                onClick={() => {
                                  setRetestingTests((prev) => ({
                                    ...prev,
                                    [item.id]: false,
                                  }))
                                  setTestInputs((prev) => ({
                                    ...prev,
                                    [item.id]: undefined,
                                  }))
                                }}
                                variant="outline"
                                size="sm"
                              >
                                Cancel
                              </Button>
                            )}
                          </div>
                        ) : (
                          <Button
                            onClick={() => setRetestingTests((prev) => ({ ...prev, [item.id]: true }))}
                            variant="outline"
                            size="sm"
                            className="w-full"
                          >
                            <Repeat className="h-3 w-3 mr-2" />
                            Retest
                          </Button>
                        )}

                        {/* Attempt History */}
                        {attempts.length > 1 && (
                          <Collapsible
                            open={expandedHistory[item.id] || false}
                            onOpenChange={(open) =>
                              setExpandedHistory((prev) => ({
                                ...prev,
                                [item.id]: open,
                              }))
                            }
                          >
                            <CollapsibleTrigger asChild>
                              <Button variant="ghost" size="sm" className="w-full h-8">
                                <ChevronDown className="h-4 w-4 mr-2" />
                                Attempt History ({attempts.length})
                              </Button>
                            </CollapsibleTrigger>
                            <CollapsibleContent className="space-y-2 mt-2">
                              {attempts.map((attempt) => (
                                <div
                                  key={attempt.id}
                                  className="flex items-center justify-between rounded-lg bg-muted p-3 text-sm"
                                >
                                  <div>
                                    <p className="font-medium">Attempt #{attempt.attempt_number}</p>
                                    <p className="text-xs text-muted-foreground">
                                      {new Date(attempt.created_at).toLocaleDateString()}
                                    </p>
                                  </div>
                                  <div className="flex items-center gap-2">
                                    <span className="font-semibold">
                                      {attempt.score}/{attempt.max_score}
                                    </span>
                                    <Badge className={getAttemptStatus(attempt.score).color}>
                                      {getAttemptStatus(attempt.score).label}
                                    </Badge>
                                  </div>
                                </div>
                              ))}
                            </CollapsibleContent>
                          </Collapsible>
                        )}
                      </div>
                    )}
                  </div>
                )
              })}
            </div>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {renderSection("Video Lectures", "", grouped.video, "Open Video", false, true)}
      {renderSection("Notes", "", grouped.notes, "Open Notes", false, false, true)}
      {renderSection("PDFs", "", grouped.pdf, "Open PDF")}
      {renderSection("Slides", "", grouped.slides, "Open Slides")}
      {!loadingScores && renderSection("Test Papers", "", grouped.test, "Open Test", true)}
      {renderSection("Test Solutions", "", grouped["test-solution"], "Open Solution")}
      {renderSection("References", "", grouped.reference, "Open Link")}
      {renderSection("Extra Resources", "", grouped.extra, "Open Resource")}
    </div>
  )
}

export function TopicPage({ topicId, studentId }: TopicPageProps) {
  const [topic, setTopic] = useState<Topic | null>(null)
  const [studyMaterials, setStudyMaterials] = useState<StudyMaterial[]>([])
  const [topicProgress, setTopicProgress] = useState<TopicProgress | null>(null)
  const [revisionSchedule, setRevisionSchedule] = useState<Revision[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [refreshTrigger, setRefreshTrigger] = useState(0)
  const [showXPPopup, setShowXPPopup] = useState<{ show: boolean; xp: number }>({ show: false, xp: 0 })
  const [showConfetti, setShowConfetti] = useState(false)

  const supabase = createClient()
  const { toast } = useToast()
  const { giveXP } = useXP(studentId, topicId)

  const fetchData = async () => {
    setLoading(true)
    setError(null)
    try {
      const [
        { data: topicData, error: topicError },
        { data: materialsData, error: materialsError },
        { data: progressData, error: progressError },
        { data: revisionsData, error: revisionsError },
      ] = await Promise.all([
        supabase.from("topics").select("*").eq("id", topicId).single(),
        supabase.from("study_materials").select("*").eq("topic_id", topicId).order("created_at", { ascending: true }),
        supabase.from("topic_progress").select("*").eq("topic_id", topicId).eq("student_id", studentId).single(),
        supabase.from("revision_schedule").select("*").eq("topic_id", topicId).eq("student_id", studentId).order("revision_number"),
      ])

      if (topicError) throw new Error(`Failed to fetch topic: ${topicError.message}`)
      if (materialsError) throw new Error(`Failed to fetch study materials: ${materialsError.message}`)
      if (progressError && progressError.code !== 'PGRST116') throw new Error(`Failed to fetch topic progress: ${progressError.message}`)
      if (revisionsError) throw new Error(`Failed to fetch revision schedule: ${revisionsError.message}`)

      setTopic(topicData)
      setStudyMaterials(materialsData || [])
      setTopicProgress(progressData)
      setRevisionSchedule(revisionsData || [])
    } catch (err) {
      setError(err instanceof Error ? err.message : "An unknown error occurred")
      console.error("Error fetching data:", err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchData()
  }, [topicId, studentId])

  // Handle XP award with popup and toast
  const handleXPAward = useCallback(
    async (eventType: "note_read" | "note_revise" | "video_watch" | "video_rewatch" | "test_score" | "revision_complete", materialId: string, score?: number) => {
      const result = await giveXP(eventType, materialId, score)

      if (!result.success) {
        if (result.error) {
          toast({
            title: "XP Blocked",
            description: result.error,
            variant: "destructive",
          })
        }
        return
      }

      if (result.xpEarned === 0) {
        toast({
          title: "No XP Earned",
          description: "Your score was too low for this test.",
          variant: "default",
        })
        return
      }

      // Show XP popup
      setShowXPPopup({ show: true, xp: result.xpEarned })
      setTimeout(() => setShowXPPopup({ show: false, xp: 0 }), 1500)

      // Show level-up if applicable
      if (result.leveledUp) {
        setShowConfetti(true)
        toast({
          title: " Level Up!",
          description: `You reached Level ${result.newLevel}!`,
        })
      } else {
        toast({
          title: "XP Earned!",
          description: `+${result.xpEarned} XP`,
        })
      }

      // Refresh level bar
      setRefreshTrigger((t) => t + 1)
    },
    [giveXP, toast]
  )

  const handleCompleteVideo = async () => {
    try {
      const { error } = await supabase.rpc("complete_topic_video", { p_student: studentId, p_topic: topicId })
      if (error) throw new Error(`Failed to complete video: ${error.message}`)
      await fetchData()
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to complete video")
      console.error("Error completing video:", err)
    }
  }

  const handleCompleteTest = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const { error } = await supabase.rpc("complete_topic_test", { p_student: studentId, p_topic: topicId, p_score: testScore })
      if (error) throw new Error(`Failed to complete test: ${error.message}`)
      setShowTest(false)
      setTestScore(0)
      await fetchData()
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to complete test")
      console.error("Error completing test:", err)
    }
  }

  const handleCompleteRevision = async (revNum: number) => {
    try {
      const { error } = await supabase.rpc("complete_topic_revision", { p_student: studentId, p_topic: topicId, p_rev: revNum })
      if (error) throw new Error(`Failed to complete revision ${revNum}: ${error.message}`)
      
      // Award XP for revision
      await handleXPAward("revision_complete", topicId)
      
      await fetchData()
      setRefreshTrigger((t) => t + 1)
    } catch (err) {
      setError(err instanceof Error ? err.message : `Failed to complete revision ${revNum}`)
      console.error(`Error completing revision ${revNum}:`, err)
    }
  }

  const dismissError = () => setError(null)

  if (loading) return <div className="flex justify-center items-center h-full"><Loader2 className="w-6 h-6 animate-spin" /></div>

  if (error) return (
    <div className="container mx-auto p-4">
      <Card className="border-destructive">
        <CardContent className="p-4">
          <div className="flex justify-between items-center">
            <div className="text-destructive">Error: {error}</div>
            <Button variant="ghost" size="sm" onClick={dismissError}>
              <X className="w-4 h-4" />
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )

  if (!topic) return <div className="text-center text-destructive">Topic not found.</div>

  return (
    <div className="container mx-auto p-4 md:p-6 space-y-6">
      <Confetti trigger={showConfetti} />
      <XPPopup show={showXPPopup.show} xp={showXPPopup.xp} />

      <LevelBar studentId={studentId} refreshTrigger={refreshTrigger} />

      {/* Section 1: Topic Header */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle className="text-2xl font-bold">{topic.name}</CardTitle>
              <CardDescription className="mt-2">{topic.description}</CardDescription>
            </div>
          </div>
        </CardHeader>
      </Card>

      <div className="grid md:grid-cols-2 gap-6">
        <div className="space-y-6">
          {/* Study Materials with XP buttons */}
          <StudyMaterials 
            materials={studyMaterials} 
            studentId={studentId} 
            topicId={topicId}
            onXPAward={handleXPAward}
          />

          {/* Section 3: Test Section */}
          <Card>
            <CardHeader>
              <CardTitle>Test Your Knowledge</CardTitle>
              {topicProgress?.test_score != null && <CardDescription>Previous Score: {topicProgress.test_score}%</CardDescription>}
            </CardHeader>
            <CardContent>
              {!showTest ? (
                <Button onClick={() => setShowTest(true)} className="w-full">
                  <Target className="w-4 h-4 mr-2" />
                  Start Test
                </Button>
              ) : (
                <form onSubmit={handleCompleteTest} className="space-y-4">
                  <Input
                    type="number"
                    placeholder="Enter your score (0-100)"
                    value={testScore}
                    onChange={(e) => setTestScore(parseInt(e.target.value))}
                    min="0"
                    max="100"
                    required
                  />
                  <div className="flex gap-2">
                    <Button type="submit" className="flex-1">Submit Score</Button>
                    <Button variant="outline" onClick={() => setShowTest(false)} className="flex-1">Cancel</Button>
                  </div>
                </form>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Section 4: Revision Section */}
        <Card>
          <CardHeader>
            <CardTitle>Training Dojo</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            {revisionSchedule.length > 0 ? revisionSchedule.map((rev) => (
              <Card key={rev.revision_number} className="p-4">
                <div className="flex justify-between items-center">
                  <div>
                    <h4 className="font-semibold">Revision {rev.revision_number}</h4>
                  </div>
                  {!rev.is_completed && (
                    <Button size="sm" onClick={() => handleCompleteRevision(rev.revision_number)}>
                      <Repeat className="w-4 h-4 mr-2" />
                      Train Now
                    </Button>
                  )}
                </div>
              </Card>
            )) : (
              <p className="text-muted-foreground text-center">No revisions scheduled yet.</p>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
